@charset "UTF-8";
body {
  margin: 0;
}

input[type~=text], input[type~=email], input[type~=phone], textarea {
  font-family: Ubuntu, sans-serif;
  font-size: 15px;
  padding-left: 15px;
}

.page {
  scroll-behavior: smooth;
}

@media (min-width: 320px) {
  .page-body {
    font-family: "Ubuntu", sans-serif;
    font-weight: 300;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
    position: relative;
    height: auto;
  }
  .page-body::after {
    content: "";
    position: absolute;
    background-image: url(../../img/mobile-intro-bg.jpg);
    background-repeat: no-repeat;
    width: 320px;
    height: 568px;
    z-index: -1;
  }
  .page-body::before {
    content: "";
    position: absolute;
    background-image: url("../../img/index-bg.jpg");
    width: 320px;
    height: 100%;
    background-attachment: fixed;
    background-size: cover;
    z-index: -1;
  }
}
@media (min-width: 1366px) {
  .page-body {
    width: 1366px;
    align-items: normal;
  }
  .page-body::after {
    background-image: url("../../img/desktop-intro-bg.jpg");
    width: 100%;
    height: 768px;
  }
  .page-body::before {
    background-image: url("../../img/index-desktop-bg.jpg");
    width: 100%;
    background-repeat: no-repeat;
    margin-top: 0;
  }
}
@media (min-width: 320px) {
  .page-header {
    display: flex;
    width: 320px;
    padding-top: 22px;
    position: fixed;
    z-index: 3;
  }

  .page-header__phone {
    display: none;
    font-size: 16px;
    font-weight: 700;
    color: #FFFFFF;
    width: 127px;
  }

  .page-header--opened-nav .page-header__phone {
    display: block;
    position: fixed;
    top: 469px;
    left: 24px;
    z-index: 3;
  }

  .page-header--opened-nav {
    background-image: url("../../img/mobile-nav-bg.jpg");
    position: fixed;
    height: 100vh;
  }

  .page-header__logo {
    width: 28px;
  }

  .page-header__button {
    width: 108px;
    height: 34px;
  }

  .page-header__nav-toggle {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    position: relative;
    width: 24px;
    background-color: #FFFFFF;
    height: 2px;
    box-shadow: 0 6px 0 #FFFFFF;
    margin-left: 62px;
    margin-top: 7px;
  }
  .page-header__nav-toggle::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #FFFFFF;
    top: 12px;
    left: 8px;
  }

  .page-header__nav-toggle--close {
    box-shadow: none;
    transform: rotate(45deg);
    top: 6px;
  }
  .page-header__nav-toggle--close::after {
    width: 24px;
    left: 0;
    top: 0;
    transform: rotate(90deg);
  }

  .page-header--scrolled {
    background-color: #000000;
    padding-bottom: 20px;
    border-bottom: 1px solid #0DD1A2;
  }
}
@media (min-width: 1366px) {
  .page-header {
    width: 1366px;
    margin: 40px auto 0 auto;
    padding-top: 0;
    align-items: center;
  }

  .page-header__nav-toggle {
    display: none;
  }

  .page-header__phone {
    display: block;
    order: 3;
    font-weight: 700;
    color: #FFFFFF;
    font-size: 16px;
    line-height: 18.38px;
    margin-left: 20px;
  }

  .page-header--scrolled {
    background-color: transparent;
    padding-bottom: 0;
    border-bottom: none;
  }
}
@media (min-width: 320px) {
  .logo {
    background-image: url("../../img/digpl-logo.svg");
    background-repeat: no-repeat;
    margin-left: 20px;
    width: 28px;
    height: 28px;
  }

  .logo__description {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (min-width: 1366px) {
