  }

  .logo:hover .logo__description {
    color: #0DD1A2;
  }

  .logo__description {
    position: static;
    width: 165px;
    height: none;
    margin: 0;
    border: none;
    padding: 0;
    white-space: normal;
    clip-path: none;
    clip: auto;
    overflow: visible;
    font-family: "Ubuntu", sans-serif;
    font-size: 18px;
    line-height: 20.68px;
    font-weight: 500;
    text-transform: uppercase;
    display: block;
    color: #FFFFFF;
    margin-left: 43px;
    margin-top: 4px;
    letter-spacing: 0.5px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

