@media (min-width: 320px) {
  .contacts {
    margin-top: 90px;
  }

  .contacts__title {
    margin: 0 0 0 16px;
    font-size: 28px;
    font-weight: 500;
    line-height: 32.17px;
    color: #FFFFFF;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 288px;
      height: 3px;
      background-color: #0DD1A2;
      top: 45px;
    }
  }
}

@media (min-width: 1366px) {
  .contacts {
    margin-top: 200px;
    margin-left: 74px;
  }

  .contacts__title {
    font-size: 44px;

    &::before {
      width: 230px;
      top: 47px;
    }
  }

  .contacts__wrapper {
    display: flex;
  }
}