@media (min-width: 320px) {
  .email-input {
    margin-top: 8px;
  }

  .email-input__input {
    width: 100%;
    border: none;
    height: 46px; 

    &::placeholder {
      font-family: "Ubuntu", sans-serif;
      font-size: 13px;
      color: #1C1C1C;
      opacity: 0.4;
    }
  }

  .email-input__input {
    &:focus {
      outline: 2px solid #0DD1A2;
    }

    // &:valid {
    //   outline: 2px solid #1DC04B;
    // }

    &:invalid {
      outline: 2px solid #F97676;
    }

    &:active {
      outline: 2px solid #0DD1A2;
    }
  }
}

@media (min-width: 1366px) {
  .email-input {
    margin-top: 0;
  }

  .email-input__input {
    width: 262px;
  }
}